.header {
  width: 100%;
  height: 60px;

  background-color: rgb(20, 20, 73);

  & .container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.open-nav-button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 40px;
  height: 40px;
}

.nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hide {
  display: none;
}
